<template>
  <div>
    <van-nav-bar title="编辑地址" left-text="返回" left-arrow @click-left="$router.go(-1)" />
    <van-address-edit
      :area-list="areaList"
      :address-info="list"
      show-postal
      show-set-default
      show-search-result
      :area-columns-placeholder="['请选择', '请选择', '请选择']"
      @save="onSave"
    />
  </div>
</template>

<script>
import areaList from "./erea.js";
export default {
  data() {
    return {
      areaList,
      list: [],
      id: null
    };
  },
  methods: {
    onSave(con) {
      con.id = this.id;
      this.$store.state.address.list[
        this.$store.state.address.modifyIndex
      ] = con;
      this.$router.go(-1)
    }
  },
  created() {
    this.$store.state.bool = false;
    this.list = this.$store.state.address.list[
      this.$store.state.address.modifyIndex
    ];
    this.id = this.$store.state.address.list[
      this.$store.state.address.modifyIndex
    ].id;

    // console.log(this.list, this.id);
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    // this.$store.state.bool = true;
  }
};
</script>

